/* Phonebook title */

.title{
    font-size: 75px;
    font-family: verdana;
    text-align: center;
    margin-right: 100px;
    /* margin-right: 100px; */
}

p{
    font-style: italic;
    font-size: 20px;
    color: darkblue;
}

body{
    margin-left: 100px;
}

h2{
    font-size: 45px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.personsForm{
    font-style: italic;
}

input{
    font-family: verdana;
    color: darkblue;
}

.form-buttons{
    color: white;
    background-color: black;
    border-radius: 30%;
}

.form-buttons:hover{
    text-decoration: underline;
    text-decoration-color: white;
    cursor: pointer;
}

.delete-button{
    font-size: 15px;
    border-radius: 20%;
}

.delete-button:hover{
    text-decoration: underline;
    text-decoration-color: black;
    cursor: pointer;
}



/* ERROR MESSAGE STYLING */
.error{
  color: red;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
  text-align: center;
  width: 400px;
  margin-left: auto;
  margin-right: auto;
}

/* SUCESS MESSAGE STYLING */
.sucess{
    color: green;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
  text-align: center;
  width: 400px;
  margin-left: auto;
  margin-right: auto;
}
